.header {
  width: 100%;
  height: 10vh;
  display: flex;
  align-items: center;
  box-shadow: 40px 40px 40px black;

}

.items {
  display: flex;
  align-items: center;
}
.header a{
  text-decoration: none;
  font-size: large;
  color: black;
  margin-right: 30px;
  font-weight: bold;
  color: rgb(106, 106, 106);
}

.menu {
  display: none;
}

/* IMAGE */
.image {
  display: flex;
  align-items: center;
  gap: 10px;
}
.image p{
  color: blue;
}

.image img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}


@media screen and (max-width: 650px) {

  /* ORIGINALS CHANGES */
  .header {
    justify-content: space-around;

  }

  .items {
    display: none;
  }

  /* MENU */
  .menu {
    display: grid;
    cursor: pointer;
    font-size: larger;
  }

  .menu p {
    margin: -6px;
  }

  .modal {
    width: 200px;
    height: 40vh;
    display: grid;
    background-color: white;
    z-index: 2;
    margin-top: 350px;
    right: 10%;
    position: absolute;
    border-radius: 10px;
    color: black;

  }

  .modal a {
    color: white;
    border: 1px solid green;
  }

  .back {
    width: 100%;
    height: 100vh;
    top: 0;
    position: absolute;
    z-index: 1;
  }
}