.poster{
  width: 100%;
  height: 80vh;
  display: flex;
  background-color: #c7e9f7;;
  /* padding: 0 20px; */
  align-items: center;
  justify-content: space-around;

}
.poster h1{
  font-size: 5rem;
}

.poster img{
  width: 500px;
  height: fit-content;
}

.poster button{
  background-color: blue;
  color: white;
  font-size: larger;
  font-weight: bold;
  border: none;
  width: 200px;
  height: 100px;

  direction: rtl;
  right: 0;
  top: 100px;
  margin-right: 10px;
  position: absolute;
}

.images{
  display: flex;
  align-items: center;
  /* justify-content: center; */
  padding: 20px;
  margin-top: -40px;
}

.imagesButton{
  display: flex;
  flex-direction: column;
}

.boxes{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  margin-top: -60px;
  z-index: 111;
}

.box{
  width: 350px;
  height: 450px;
  background-color: darkblue;
  margin: 10px;
  padding: 50px;
  box-sizing: border-box;
}
.box button{
  width: 100%;
  height: 5vh;
  background-color: yellowgreen;
}


@media screen and (max-width: 650px) {
  .poster, .boxes{
    flex-direction: column;
  }
  .poster{
  height: 90vh;

  }
  .poster h1{
    font-size: 3.5rem;
  }
  .boxes{
  margin-top: -50px;
}
.images{
  display: grid;
  padding: 4px;
  }
  .poster img{
    width: 100%;
  }
  .poster button{
    width: 150px;
    height: 50px;
  } .poster button{
    top: 80px;
 }
}


@media screen and (min-width: 650px) and (max-width: 860px) {
  .poster h1{
    font-size: 4rem;
  }
  .poster img{
    width: 400px;
  }
 
}