.body{
  display: grid;
  align-content: center;
  justify-content: center;
}

.register{
  display: flex;
  flex-direction: column;
}
.register button{
  width: 150px;
  height: 40px;
}

.err{
  min-width: 310px;
  color: red;
}

.register input{
  padding: 10px;
  margin: 10px;
  font-size: larger;
}


.pass{
  width: 250px;
  display: flex;
  /* justify-content: space-around; */
}
.weak{
  width: 33%;
  height: 3px;
  background-color: red;
}
.moderate{
  width: 33%;
  height: 3px;
  background-color: yellow;
}
.strong{
  width: 33%;
  height: 3px;
  background-color: green;
}
.white{
  width: 33%;
  height: 3px;
}